@import url("https://fonts.googleapis.com/css2?family=Cantora+One&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Jost:wght@300&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Limelight&family=Rubik+Microbe&display=swap');

html,
body {
  margin: 0px;
  font-family: "Jost", "Cantora One", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: auto;
  width: auto;
  background-color: whitesmoke;
  background: rgb(131,58,180);
  background: linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%);
  
  overflow-x: hidden;
  overflow-y: hidden;
}


button {
  aspect-ratio: 85/45;
  height: 7vh;
  min-width: 45px;
  max-width: 85px;
  background-color: rgba(177, 177, 177, 0.733);
  border-radius: 6%;
  font-size: 2vh;
  transition: all 125ms ease-in-out;
  text-align: center;
  border: 0em rgba(255, 255, 255, 0);
}

button:hover {
  scale: 110%;
  background-color: rgb(255, 255, 255);
  color: rgb(48, 31, 31);
  border: 2px solid rgb(48, 31, 31);
}



.App {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  width: 100%;
  max-height: 10000vh;
  font-size: 2vh;
  font-family: 'Limelight', 'Rubik Microbe', "Jost", sans-serif, cursive;
  background-color: rgb(5, 2, 21);

}

.container {
  max-height: 100vh;
	overflow-y: scroll;
  scroll-snap-type: y mandatory;
  overflow-x: hidden;
  transition: 40ms ease-in-out;
  scroll-behavior: smooth;
}

.panel {
  scroll-snap-align: start;
  height: 100vh;
  
}


.Header {
  position: relative;
  width: 100%;
  max-width: fit-content;
  height: 10vh;
  display: inline-flex;
  flex-direction: row;
  justify-content: left;
  font-size: 2.5vh;
  font-family: "Cantora One", sans-serif;
}

.HeaderLogo {
  margin: 5px;
  cursor: pointer;
}

.HeaderLogo:hover {
  scale: 1.05;
  transition: 120ms ease-in-out;
}

/* .icon:hover {
  scale: 1.02;
  transform: translateY(-5);
  transition: 120ms ease-in-out;
  animation-delay: 50ms;
} */

.Navbar{
  width: 100%;
  height: 10vh;
  display: inline-flex;
  flex-direction: row;
  align-items: last baseline;
  flex-wrap: nowrap;
  justify-content: space-around;
  position: fixed;
  top: 5vh;
  z-index: 12;
  background-color: #ececec4a;
}

Canvas {
  height: 100%;
  margin: 0;
  padding: 0;
  width: 100%;
}

.Hero {
  position: relative;
  width: 100%;
  height: 100vh;
  transition: all 2s ease-in-out;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #bd5050;
  padding-inline: 3vw;
}

.heroText svg{
  width: 8vw;
  height: 8vw;
  animation: wave 1920ms 20ms infinite ease-in;
}

.heroText svg:hover{
  animation: none;
}

@keyframes wave {
  0%{ transform: rotateX(1deg) rotateZ(10deg); }
  45%{ transform: rotateX(-1deg) rotateZ(-20deg) scale(0.9) }
  100%{ transform: rotateX(1deg) rotateZ(10deg); }
}

.heroText {
  color: aliceblue;
  font-family: "Jost", sans-serif, 'cursive';
  font-size: 6vw;
  line-height: 9vh;
  transition: all 0.5s ease-in-out;
  max-width: fit-content;
}

.Heroline1 {
  letter-spacing: 1vw;
  font-size: 6vw;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.Heroline2 {
  letter-spacing: 0.5vw;
  font-size: 5vw;
}

.Heroline3 {
  letter-spacing: 1.2vw;
  font-size: 5vw;
  font-weight: 700;
}

.Context {
  position: relative;
  height: 100vh;
  width: 100%;
}

.Contact {
  position: relative;
  height: 70vh;
  width: 100vw;
  padding-top: 10vh;
  display: flex;
  flex-direction: row;
  margin-inline: 3%;
}

#CommentContainer {
  position: relative;
  height: 80%;
  width: 40%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  overflow-y: scroll;
  overflow-x: hidden;
}

.commentUI {
  position: relative;
  height: 20%;
  width: 70%;
  margin-inline-start: 5%;
  display: flex;
  flex-direction: column;
  background-color: rgb(185, 185, 185);
  padding: 5%;
  border-radius:  3% 2% 3% 2%;
}

.Footer {
  position: relative;
  height: 100vh;
  width: 100vw;
  bottom: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-between;
  background-color: rgb(13, 13, 13);
  color: azure;
}


.icon32 {
  height: 32px; width: 32px;
}


.blankDiv {
  max-width: 100%;
  max-height: 100%;
}



.ErrorPage {
  height: 98vh;
  width: 98vw;
  margin-inline: 1vw;
  margin-top: 1vh;
  background-color: rgb(29, 21, 49);
  color: rgb(239, 227, 207);
  font-size: larger;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 5%;
}

.ErrorPage h1 {
  font-size: 10vh;
}

.ErrorPage h2 {
  color: azure;
}

.Socials {
  display: flex;
  flex-wrap: nowrap;
  gap: 5px;
}

.SoLinkRow{
  flex-direction: row;
  position: absolute;
  right: 0; bottom: 0;
  margin-bottom: 20px;
}

.SoLinkCol{
  flex-direction: column;
  position: fixed;
  right: 0; bottom: 0;
  margin-right: 2.5vw;
}


.Socials svg {
  padding: 4px;
  fill: bisque;
}


.Socials svg:hover {
  scale: 1.1;
  background: #ececec4a;
  background-blend-mode: color-dodge;
  border-radius: 10%;
  transition: all 0.2 ease-in-out;
  fill:rgb(174, 184, 248)
}

.copyright {
  background-color: rgba(22, 28, 35, 0.668);
  color: rgb(169, 228, 219);
  width: 100%;
  text-align: center;
  align-self: baseline;
  position: absolute;
  bottom: 0;
}

.Working {
  height: 99vh;
  width: 99vw;
  background-color: rgb(12, 4, 32);

}

.LNavBar {
  height: inherit;
  width: 15%;
  position: fixed;
  left: 0;
  margin-left: 4px;
  background-color: #fed75a;
}

.bordL {
  border-left: 1px white;
  border-width: 1px;
}

.bordR {
  border-right: 1px white;
  border-width: 1px;
}

.flexRow {
  display: flex;
  flex-direction: row;
}

.flexcol {
  display: flex;
  flex-direction: column;
}

.jselfRight {
  justify-self: flex-end;
}

.ProjectCarosol {
  height: 100vh;
  width: max-content;
  background-color: rgb(164, 112, 43);
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow-x: hidden;
}


.blueDiv, .redDiv , .greenDiv {
  height: 90vh;
  width: 100vw;
 }
.blueDiv {
  background-color: rgb(30, 169, 169);
}

.redDiv {
  background-color: rgb(169, 93, 30);
}
.greenDiv {
  background-color: rgb(93, 175, 84);
}

.prjs {
  width: 50vh;
  height: 50vh;
  border-radius: 5%;
  background-color: bisque;
  margin-left: 25%;
  margin-top: 25vh;
}

.RatingForm {
  background-color: aliceblue;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  aspect-ratio: 5/4;
  width: 45vh;
  border-radius: 5%;
  padding: 5vh;
  position: relative;
  align-self: center;
  margin-inline: 5%;
}

.footdesc {
  max-width: fit-content;
  margin-inline: 2vw;
}

.CloseBTNWrapper {
  transition: 89ms ease-in-out;
  position: absolute;
  display: flex;
  bottom: 0;
  right: 0;
  margin-right: 2vw;
  margin-bottom: 2vh;
  max-width: 120px;
}

.CloseBTN {
  position: absolute;

  min-height: 60px;
  aspect-ratio: 1/1;
  height: 4vh;
  border-radius: 5%;
  background-color: white;
  z-index: 4831233;
}

#shape {
  will-change: none;
}
#test {
  height: 57px;
  width: 24px;
  -webkit-clip-path: url(#shape);
  clip-path: url(#shape);
  will-change: none;
}

